import fetch from "auth/FetchInterceptor";

const userService = {};

userService.getUserById = function (id) {
  return fetch.get(
    `/users/${id}`
    // method: "get",
  );
};

userService.getUsers = function () {
  console.log("fetching");
  const res = fetch.get("/users");
  console.log(res);
  return res;
};

export default userService;
